﻿html{
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #eee;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: #5f1713;
    }

    scroll-behavior: smooth;

    overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba(#eee,1);
    // scroll-behavior: smooth;
}

body{
    font-family: 'ar-reg';
    overflow-y: hidden;
}



