.container {
  width: 1240px;
}






.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #5f1713;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px #5f171380;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    button {
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      left: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      &:hover {
        color: #5f1713;
      }
    }
  }
}






/*------ upperbar start ------*/
.upper-bar {
  padding: 15px 0;
  .zx-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .cur-switcher {

  }
  .lang-switcher, .cur-switcher {
    position: relative;
    font-family: ar-reg;
    font-size: 14px;
    color: #999999;
    margin: 0 10px 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "\f0d7"; /* FontAwesome Unicode */
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 600;
        font-size: 11px;
        color: #999999;
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 50%;
      z-index: 9;
      transform: translateX(-50%);
      width: 100px;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 8px 0;
      transition: all .5s ease-in-out;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      a {
        display: block;
        margin: 3px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #5f1713;
        }
      }
      span {
        padding: 0 2px;
      }
    }
    &:hover {
      > span {
        color: #5f1713;
        &::after {
          color: #5f1713;
        }
      }
      .switch {
        top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
/*------ upperbar end ------*/




/*------ navbar start ------*/
.nav-bar {
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 0;
  }
  .container {
    position: relative;
  }
  .logo-container {
    width: 230px;
    .logo {
      width: 230px;
    }
  }
  .nav-search {
    position: relative;
    width: 504px;
    height: 54px;
    background-color: #ffffff;
    border-style: solid;
    border-width: 1px;
    border-color: #d7d7d7;
    border-radius: 3px;
    transition: all .3s ease-in-out;
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      background-color: transparent;
      padding: 0 20px;
      transition: all .3s ease-in-out;
      &::placeholder {
        font-family: ar-reg;
        font-size: 15px;
        color: #999999;
      }
      &:hover, &:focus {
        border: 1px solid #5f1713;
        border-radius: 3px;
      }
    }
    button {
      font-size: 16px;
      color: #131313;
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translateY(-50%);
      transition: all .3s ease-in-out;
      &:hover {
        color: #5f1713;
      }
    }
  }
  .icons {
    width: 230px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    button {
      font-size: 18px;
      color: #131313;
      position: relative;
      padding: 0;
      margin: 0 0 0 20px;
      transition: all .3s ease-in-out;
      display: flex;
      justify-content: center;
      align-items: center;
      span, a {
        font-family: ar-md;
        font-size: 16px;
        color: #131313;
        padding: 0 1px;
        transition: all .3s ease-in-out;
      }
      a {
        margin: 0 5px 0 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #5f1713;
        }
      }
      &:last-child {
        margin: 0;
      }
      &:hover {
        color: #5f1713;
        span {
          color: #5f1713;
        }
      }
    }
    .menu-icon, .search-icon {
      display: none;
    }
    .cart-icon {
      width: 50px;
      height: 50px;
      background-color: #131313;
      border-radius: 50%;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      .counter {
        position: absolute;
        top: -7px;
        right: -7px;
        width: 26px;
        height: 26px;
        color: rgb(255, 255, 255);
        border-style: solid;
        border-width: 2px;
        border-color: rgb(255, 255, 255);
        border-radius: 50%;
        background-color: #5f1713;
        font-family: ar-reg;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
      }
      &:hover {
        color: #fff;
        background-color: #5f1713;
        .counter {
          transform: rotateY(360deg);
          background-color: #131313;
        }
      }
    }
  }
  
}


/*------ navbar end ------*/



/*------ navlist start ------*/
.nav-list {
  width: 100%;
  background-color: #5f1713;
  padding: 14px 0;
  margin: 15px 0 0 0;
  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      position: relative;
      font-family: ar-md;
      font-size: 16px;
      color: #fff;
      text-transform: uppercase;
      margin: 0 15px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #c4625d;
        .sub-cat-hover {
          opacity: 1;
          visibility: visible;
          top: 30px;
        }
      }
    }
  }

  .sub-cat {
    .sub-cat-hover {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 50%;
      transform: translateX(-50%);
      min-width: 140px;
      min-height: 200px;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
      padding: 5px 13px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      z-index: 9;
      transition: all .5s ease-in-out;
      li {
        width: 100%;
        transition: all .3s ease-in-out;
        font-family: ar-reg;
        font-size: 16px;
        color: #5e5e5e;
        border-bottom: 1px solid #e6e6e6;
        padding: 7px 0;
        margin: 0;
        text-align: center;
        &:last-child {
          border: none;
        }
        &:hover {
          color: #5f1713;
        }
      }
    }
  }
}
/*------ navlist end ------*/





/*------ header start ------*/
.header {
    .owl-carousel {
      position: relative;
      .owl-nav {
        .owl-prev, .owl-next {
          outline: none;
          width: 50px;
          height: 50px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 14px;
          background-color: rgba(0, 0, 0, 0.5);
          // border-radius: 50%;
          color: rgb(255, 255, 255);
          transition: all .5s ease-in-out;
          &:hover {
            color: #1a1a1a;
            background-color: #fff;
          }
        }
        .owl-prev {
          right: 0px;
        }
        .owl-next {
          left: 0px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 15px;
          height: 5px;
          background-color: #00000080;
          border-radius: 50px;
          outline: none;
          margin: 0 2.5px;
          &.active {
            background-color: #fff;
          }
        }
      }
    }
   
}

/*------ header end ------*/





/*------ servise start ------*/
.servs {
  margin: 20px 0;
  .zx-container {
    height: 100px;
    border-width: 1px;
    border-color: rgb(196, 196, 196);
    border-style: dashed;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    .row {
      width: 100%;
    }
  }
  .service {
    display: flex;
    justify-content: center;
    align-items: center;
    button {
      font-size: 24px;
      color: #5f1713;
      width: 66px;
      height: 66px;
      background-color: rgb(234, 234, 234);
      border-radius: 50%;
      cursor: default;
    }
    span {
      font-family: ar-reg;
      font-size: 15px;
      color: #131313;
      padding: 0 8px;
    }
  }
}
/*------ servise end ------*/





/*------ header panners start ------*/
.header-panners {
  .header-panner {
    overflow: hidden;
    margin: 0 0 17px 0;
    &:last-child {
      margin: 0;
    }
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        transform: scale(1.05);
      }
    }
  }
}
/*------ header panners end ------*/



/*--- card start ---*/
.card {
  background-color: rgb(255, 255, 255);
  border-style: solid;
  border-width: 1px;
  border-color: rgb(224, 224, 224);
  background-color: rgba(14, 79, 54, 0);
  width: 266px;
  min-height: 314px;
  overflow: hidden;
  margin: 0 auto;
  &:hover {
    img {
      transform: scale(1.05);
    }
  }
  .card-img {
    margin: 0 0 0px 0;
    overflow: hidden;
    img {
      transition: all .5s ease-in-out;
    }
  }
  .card-body {
    padding: 0 35px;
  }
  .title {
    font-family: ar-md;
    font-size: 16px;
    color: rgb(19, 19, 19);
    margin: 20px 0 5px 0;
    text-align: center;
    border-bottom: 1px solid #f0f0f0;
    padding: 0 0 5px 0;
  }
  .price {
    font-family: ar-bold;
    font-size: 16px;
    color: #5f1713;
    margin: 0 0 15px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #f0f0f0;
    padding: 0 0 5px 0;
    .old {
      font-family: ar-reg;
      font-size: 16px;
      color: #808080;
      text-decoration: line-through;
      margin: 0 5px;
    }
  }
  .card-icons {
    text-align: center;
    a {
      margin: 0 13px;
      font-size: 16px;
      color: rgb(162, 162, 162);
      transition: all .5s ease-in-out;
      &:hover {
        color: #5f1713;
      }
    }
  }
}

/*--- card end ---*/








/*------ sp offers start ------*/
.sp-offers {
  .sp-offers-header {
    text-transform: uppercase;
    margin: 30px 0 10px 0;
    
    background-color: rgb(242, 242, 242);
    height: 55px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    h3 {
      font-family: ar-bold;
      font-size: 16px;
      color: rgb(255, 255, 255);
      margin: 0;
      width: 165px;
      height: 100%;
      background-color: #5f1713;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .show-more {
      font-family: ar-reg;
      font-size: 14px;
      color: #131313;
      margin: 0 24px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #5f1713;
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: block;
      width: 15px;
      position: absolute;
      top: -37px;
      left: 30px;
      .owl-prev, .owl-next {
        outline: none;
        // width: 30px;
        // height: 30px;
        position: absolute;
        top: 40%;
        transform: translateY(-50%);
        font-size: 16px;
        color: rgb(190, 203, 209);
        transition: all .5s ease-in-out;
        
        &:hover {
          color: #5f1713;
        }
      }
      .owl-prev {
        right: -10px;
      }
      .owl-next {
        left: -10px;
      }
