/*------ responsive ------*/
@media (min-width: 1600px) {
  .owl-carousel .owl-item img {
    width: 100%;
  }
  .container {
    width: 1440px;
  }
  .nav-bar .logo-container {
    width: 330px;
    .logo {
      width: 330px;
    }
  }
  .nav-bar .nav-search {
    width: 604px;
  }
  .nav-bar .icons {
    width: 330px;
  }
}
@media (max-width: 992px) {
  .container {
    width: unset;
  }
  .pre-loader .content {
    width: 230px;
  }
  .upper-bar {
    display: none;
  }
  .nav-bar .icons button {
    font-size: 22px;
    span {
      display: none;
    }
    &.user-icon {
      display: none;
    }
  }
  .nav-bar .icons .cart-icon {
    .counter {
      padding: 3px 0 0 0;
    }
  }
  .nav-bar .icons .menu-icon, .nav-bar .icons .search-icon {
    display: block;
  }
  .nav-list {
    display: none;
  }
  .nav-bar .logo-container .logo {
    width: 130px;
  }


  .nav-bar .nav-search {
    display: none;
  }





  .fixed-menu .lang-switcher {
    // position: absolute;
    // top: 15px;
    // left: 0px;
    position: relative;
    font-family: ar-reg;
    font-size: 12px;
    color: #939393;
    margin: 0 0 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      margin: 0 5px;
    }
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: ""; 
        font-family: "Linearicons-Free";
        font-weight: 600;
        font-size: 8px;
        color: #939393;
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 50%;
      z-index: 9;
      transform: translateX(-50%);
      width: 90px;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 5px 0;
      transition: all .5s ease-in-out;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      a {
        display: block;
        margin: 4px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #5f1713;
        }
      }
      span {
        padding: 0 2px;
      }
    }
    &:hover {
      > span {
        color: #5f1713;
        &::after {
          color: #5f1713;
        }
      }
      .switch {
        top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .fixed-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 260px;
      height: 100%;
      position: absolute;
      top: 0;
      right: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        right: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 17px;
        position: absolute;
        top: 20px;
        left : 15px;
      }
    }
    .main-list {
      margin: 35px 0px;
      transform: translateX(-50%);
      transition: all .5s ease-in-out;
      .slide {
        cursor: pointer;
        position: relative;
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: #0f0f0f;
          position: absolute;
          top: 8px;
          left: 10px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #5f1713;
          &::after {
            content: "";
          }
        }
      }
      > li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 10px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        transform: translateX(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 9px;
          right: 15px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #000;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-reg;
        font-size: 16px;
        color: #444;
        margin: 5px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
    .sub-list-copy {
      padding: 5px 15px;
      display: none;
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
  }



  .header {
    .owl-carousel {
      .owl-nav {
        display: none;
      }
      .owl-dots {
        bottom: 10px;
        .owl-dot {
          margin: 0 5px;
        }
      }
    }
  }



  .servs {
    margin: 30px 0;
  }
  .servs .zx-container {
    flex-direction: column;
    height: unset;
    padding: 15px 0;
  }
  .servs .service {
    padding: 10px 0;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    span {
      padding: 5px 0;
    }
  }
  

  .header-panners .header-panner {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
  }





  .sp-offers .sp-offers-header {
    justify-content: space-between;
  }
  .sp-offers {
    margin: 0 0 50px 0;
    .card {
      margin: 0 auto;
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        display: block;
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: -30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 15px;
          height: 5px;
          background-color: #00000080;
          border-radius: 50px;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: #5f1713;
          }
        }
      }
    }
  }


  
  .panner {
    margin: 70px 0 5px 0;
    .img-container {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 5px 0;
    }
  }
  


  .footer {
    margin: 70px 0 0 0;
    padding: 40px 10px 0 10px;
    .zx-container {
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }
  .footer .footer-icons {
    justify-content: center;
    align-items: center;
    margin: 25px 0 0 0;
  }


  


  .footer {
    .footer-lorem {
      width: 80%;
      margin: 0;
      img {
        margin: 0 auto;
      }
      p {
        text-align: center;
        margin: 30px 0 40px 0;
      }
    }
    .footer-list {
      width: 100%;
    }
    .list-header {
      margin: 0;
      padding: 10px 0;
      position: relative;
      &::after {
        content: "";
        font-family: "Linearicons-Free";
        font-size: 12px;
        color: #000;
        position: absolute;
        top: 50%;
        left: 0px;
        transform: translateY(-50%);
        transition: all .5s ease-in-out;
      }
      &.active {
        &::after {
          content: "";
        }
      }
    }
    .footer-slide {
      display: none;
      padding: 0 0 0 0;
      margin: unset;
      li {
        margin: unset;
        padding: 0 0 10px 0;
      }
    }
    .info-header {
      text-align: center;
      margin: 40px 0 20px 0;
    }
    .info-list {
      a {
        justify-content: center;
        align-items: center;
      }
    }


    .copy-container {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin: 30px 0 25px 0;
    }
    .copyrights {
      order: 2;
      margin: 20px 0 0 0;
      text-align: center;
      flex-direction: column;
      span {
        padding: 3px 0;
        // &:nth-of-type(2) {
        //   display: none;
        // }
      }
    }
    .sp-payment {
      margin: 40px 0 0 0;
    }
    .payment {
      order: 1;
      padding: 20px 0 0px 0;
    }
  }
}



@media (max-width: 480px) {
  
  .search-container .search {
    width: 90%;
  }

  
  .footer {
    padding: 40px 10px 0 10px;
  }


  .footer .footer-lorem {
    width: 100%;
  }
  .footer .copy-container {
    margin: 0 0 10px 0;
  }


}


/*------ direction ------*/
html[dir="ltr"] {
  .nav-bar .nav-search button {
    left: unset;
    right: 20px;
  }
  .nav-bar .icons button a {
    margin: 0 0 0 5px;
  }
  .nav-bar .icons button {
    margin: 0 20px 0 0;
    &:last-child {
      margin: 0;
    }
  }
  .sp-offers .owl-carousel .owl-nav {
    left: unset;
    right: 30px;
  }
  .footer .footer-lorem {
    margin: 0 50px 0 0;
  }


  .search-container .search button {
    left: unset;
    right: 20px;
  }
  .footer ul i {
    margin: 0 15px 0 0;
  }
  @media (max-width: 992px) {
    .nav-bar .icons .cart-icon .counter {
      right: -7px;
    }
    .fixed-menu .menu {
      right: unset;
      left: -280px;
      &.show {
        left: 0;
      }
    }
    .fixed-menu .menu-header .close {
      left: unset;
      right: 15px;
    }
    .fixed-menu .main-list .slide::after {
      left: unset;
      right: 10px;
    }
    .fixed-menu .main-list .slide.active {
      &::after {
        content: "";
      }
    }
    .footer .list-header::after {
      left: unset;
      right: 0;
    }
    .footer .list-header.active {
      &::after {
        content: "";
      }
    }
    .footer .footer-lorem {
      margin: 0;
    }
  }
}